<template>
  <!---->
  <div id="projectPush" v-loading.fullscreen.lock="loading" element-loading-text="">
    <el-dialog :visible="dialogPush" :before-close="handleClose">
      <!---->
      <span slot="title" class="dialog-title clearfix">
        <div class="lines fl"></div>
        <div class="title fl"></div>
        <div class="lost fl"><i>{{pushCount}}</i></div>
        <div class="img fl"><img src="../../../assets/images/why.png"></div>
      </span>

      <!---->
      <div>
        <div class="text1"></div>
        <div class="text2">{{project_name}}</div>
      </div>

      <!---->
      <el-form :inline="true" :model="investor" class="demo-form-inline pushInvestor" label-position="top">
        <el-form-item label="">
          <el-select v-model="allShow" filterable
                     style="width: 586px;"
                     remote placeholder=""
                     multiple @remove-tag="removeTag"
                     :remote-method="remoteMethod"  popper-class="popper">
            <el-option v-for="(item,index) in allCheck" :key="item.card.card_id" :label="item.card.user_real_name" :value="item.card.user_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button style="margin-top: 25px;" type="primary" @click="customerAdd"></el-button>
        </el-form-item>
      </el-form>

      <!--tab-->
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <!---->
          <el-tab-pane label="" name="myContacts">
            <el-table
              v-loading="loading"
              element-loading-text=""
              ref="myContacts"
              :data="myContacts"
              tooltip-effect="dark"
              style="width: 100%;font-size: 12px;"
              max-height="430"
              @selection-change="tableCheck1"
              :row-class-name="tableRowClassName">
              <!--1-->
              <el-table-column width="64" type="selection">

              </el-table-column>
              <!--2-->
              <!--<el-table-column width="64">
                <template scope="scope">
                    <el-checkbox @change="check1" :name="scope.row.card.user_real_name"></el-checkbox>
                </template>
              </el-table-column>-->
              <!---->
              <el-table-column
                label=""
                prop="name"
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_real_name}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_real_name}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_company_career}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_company_career}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_company_name}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_company_name}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_email}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_email}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt" >{{scope.row.card.user_invest_industry_str}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_invest_industry_str}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.match}}</div>
                    </div>
                    <div>
                      {{scope.row.match}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>

          <!---->
          <el-tab-pane label="" name="netContacts">
            <el-table
              v-loading="loading"
              element-loading-text=""
              ref="netContacts"
              :data="netContacts"
              tooltip-effect="dark"
              style="width: 100%;font-size: 12px;"
              max-height="430"
              @selection-change="tableCheck2"
              :row-class-name="tableRowClassName">
              <el-table-column type="selection" width="64"></el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_real_name}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_real_name}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_company_career}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_company_career}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_company_name}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_company_name}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_email}}</div>
                    </div>
                    <div>
                      {{scope.row.card.user_email}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.card.user_invest_industry_str}}</div>
                    </div>
                    <div>{{scope.row.card.user_invest_industry_str}}</div>
                  </el-tooltip>
                </template>
              </el-table-column>
              <!---->
              <el-table-column
                label=""
                min-width="100">
                <template scope="scope">
                  <el-tooltip placement="top">
                    <div slot="content">
                      <div class="tips-txt">{{scope.row.match}}</div>
                    </div>
                    <div>
                      {{scope.row.match}}
                    </div>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>

      <!---->
      <el-form label-position="top" label-width="80px" ref="email" :model="email">
        <el-form-item label="" prop="title">
          <el-input v-model="pushTitle" placeholder="-"></el-input>
        </el-form-item>
        <el-form-item label="" prop="main">
          <el-input type="textarea"
                    v-model="pushBody"
                    placeholder=""
                    :autosize="{ minRows: 4, maxRows: 7}"></el-input>
        </el-form-item>
      </el-form>

      <!---->
      <span slot="footer" class="dialog-footer">
        <el-button @click="preview"></el-button>
        <el-button type="primary" @click="push"></el-button>
      </span>

      <!---->
      <!--<customeraddcontacts :dialog-form-visible="dialogFormVisible"></customeraddcontacts>-->

      <!--2-->
      <el-dialog class="customerAddForm"  title="" :visible.sync="dialogFormVisible" :modal='false' size="full" :close-on-click-modal="false">
        <el-form :model="customerAddForm" ref="customerAddForm">
          <el-form-item label="" :label-width="formLabelWidth" prop="email" :rules="[{ required: true, message: ''}]">
            <el-input v-model="customerAddForm.email" auto-complete="off"  placeholder="" :rules="[{ required: false}]"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="name">
            <el-input v-model="customerAddForm.name" auto-complete="off" placeholder="" :rules="[{ required: false}]"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="mobile">
            <el-input v-model="customerAddForm.mobile" auto-complete="off" placeholder="" :rules="[{ required: false}]" type="number"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="company">
            <el-input v-model="customerAddForm.company" auto-complete="off" placeholder="" :rules="[{ required: false}]"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="brand">
            <el-input v-model="customerAddForm.brand" auto-complete="off" placeholder="" :rules="[{ required: false}]"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="career">
            <el-input v-model="customerAddForm.career" auto-complete="off" placeholder="" :rules="[{ required: false}]"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="cancelAdd"> </el-button>
          <el-button type="primary" @click="certainAdd"> </el-button>
        </div>
      </el-dialog>
    </el-dialog>
<!---->
  </div>
</template>

<script type="text/ecmascript-6">
  import customerAddContacts from '../../../components/customerAddContacts.vue'
  export default {
  props: ["dialogPush",'proid','proName'],
  data () {
    var checkEmail = (rule, value, callback) => {
      if (this.$tool.getNull(value)) {
        return callback(new Error(''));
      }
      setTimeout(() => {
        if (!this.$tool.checkEmail(value)) {
          callback(new Error(''));
        } else {
          callback();
        }
      }, 300);


    };//
    var checkTitle = (rule, value, callback) => {
      if (this.$tool.getNull(value)) {
        return callback(new Error(''));
      } else {
        callback();
      }
    };//
    return {
      project_name:this.proName,
      project_id:this.proid,
      close:false,//
      loading: false,
      //tab
      activeName: 'myContacts',
      //
      myContacts:[],
      //
      netContacts:[],
      //(computed)
      myContactsCheck:[],
      netContactsCheck:[],
      //(computed)
      myContactsShow:[],
      netContactsShow:[],
      //
      dialogFormVisible:false,
      //
      customerAddForm: {
        email:'',
        name: '',
        mobile:'',
        company:'',
        brand:'',
        career:'',
      },
      formLabelWidth: '74px',
      //
      pushCount:5,
      //
      pushTitle:'',
      pushBody:'',
      //tab
      activeTab:'myContacts',
      //input
      filterString:'',
      //
      myCheckList:[],



      emailRule: {validator: checkEmail, trigger: 'blur'},
      titleRule: {validator: checkTitle, trigger: 'blur'},
      email:{
        title:',',//
        main:'',//
      },
      email2:{
        nameEmail:'',//()
      },
      user:{
        user_real_name:'',
        user_company_career:'',
        user_company_name:'',
      },
      projectList:[],//
      projectAll:[],//
      list: [],
      states: ["Alabama"],
      projectRadio:'',
      investor:{
        name:'',
      },
    }
  },
  components:{customerAddContacts},
  methods: {
    //
    getMyContacts(){
      this.$http.post(this.URL.getConnectUserSortByMatch, {
        user_id: localStorage.user_id,
        project_id: this.project_id,
        search:this.filterString,
      }).then(res => {
        if(res.data.status_code===2000000){
//          console.log('', res.data.data)
          this.myContacts=res.data.data;
        }else{
//          console.log(res.data.error_msg)
        }
      })
    },
    //
    getNetContacts(){
      this.$http.post(this.URL.getAllConnectUserSortByMatch, {
        user_id: localStorage.user_id,
        project_id: this.project_id,
        search: this.filterString,
      }).then(res => {
        if(res.data.status_code===2000000){
//          console.log('',res.data.data)
          this.netContacts=res.data.data;
        }else{
//          console.log(res.data.error_msg)
        }
      })
    },
    //
    getPushCount(){
      this.$http.post(this.URL.pushCount,{
        user_id:localStorage.user_id
      }).then(res=>{
        if(res.data.status_code===2000000){
          this.pushCount=res.data.data.push_count.remain_times;
        }
      })
    },
    //,,,,
    initData(){
      this.getMyContacts();
      this.getNetContacts();
      this.getPushCount();
      this.pushTitle='';
      this.pushBody='';
    },
    //
    handleClick(tab, event) {
//      console.log(tab.name)
      this.activeTab=tab.name
    },
    //
    tableRowClassName(row, index) {
      if (index%2 === 1) {
        return 'info-row';
      }
      return '';
    },
    //()
    customerAdd(){
     this.dialogFormVisible=true;
    },
    //
    cancelAdd(){
      this.$refs['customerAddForm'].resetFields();
      this.dialogFormVisible=false;
    },
    //
    certainAdd(){
      var form=this.customerAddForm;
      if(!form.email){
        this.$tool.error('')
      }else if(!this.$tool.checkEmail(form.email)){
        this.$tool.error('')
      }else{
        this.$http.post(this.URL.createUserCard,{
          user_id:localStorage.user_id,
          user_email:form.email,
          user_real_name:form.name,
          user_mobile:form.mobile,
          user_company_name:form.company,
          user_company_career:form.career,
          user_brand:form.brand
        }).then(res=>{
          console.log(res)
          if(res.data.status_code===2000000){
            this.$refs['customerAddForm'].resetFields();
            this.dialogFormVisible=false;
            this.getMyContacts();
            this.getNetContacts();
            console.log(this.myContactsShow,this.myContactsCheck)
          }
        })
      }
    },
    //
    tableCheck1(val){
      console.log(val)
      this.myContactsCheck=val;
      let arry=[];
      val.forEach(x=>{
        arry.push(x.card.user_real_name)
      })
      this.myContactsShow=arry;
    },
    //
    tableCheck2(val){
      this.netContactsCheck=val;
      let arry=[];
      val.forEach(x=>{
        arry.push(x.card.user_real_name)
      })
      this.netContactsShow=arry;
    },
    //
    preview(){
      this.$emit('changeall',false);
    },
    //
    push(){
      let dealData=[]
      this.allCheck.forEach(x=>{
        if(x.type==='card'){
          dealData.push([x.card.card_id,x.type,x.card.user_email])
        }else{
          dealData.push([x.card.user_id,x.type,x.card.user_email])
        }
      })
      console.log(dealData)
      if(dealData.length===0){
        this.$tool.error('')
      }else if(dealData.length>this.pushCount){
        console.log(dealData.length,this.pushCount)
        this.$tool.error('')
      }else{
        this.$http.post(this.URL.pushProject,{
          user_id:localStorage.user_id,
          project_id:this.project_id,
          title:this.pushTitle,
          body:this.pushBody,
          receives:dealData
        }).then(res=>{
          if(res.data.status_code===2000000){
            this.$tool.success('');
            this.initData();
            this.myContactsCheck=[];
            this.netContactsCheck=[];
          }
        })
      }
    },
    /*//
    remoteMethod(query) {
      if (query !== '') {
        this.load = true;
        setTimeout(() => {
          this.load = false;
          this.myContacts = this.list.filter(item => {
            return item.label.toLowerCase()
                .indexOf(query.toLowerCase()) > -1;
          });
        }, 200);
      } else {
        this.myContacts = [];
      }
    },*/
    //
    remoteMethod(query) {
      console.log(query)
      this.filterString=query;
      console.log(this.activeTab)
      if(this.activeTab==="myContacts"){
        this.getMyContacts()
      }else{
        this.getNetContacts()
      }
    },
    //
    removeTag(e){
      console.log(e);
      this.myContactsShow.forEach((x,index)=>{
        if(x===e.value){
          this.myContactsShow.splice(index,1)
          this.myContactsCheck.splice(index,1)
        }
      })
    },
    //
    nameFilter(x){
      if(x.card.user_real_name.indexOf(this.filterString)!=-1){
        return x
      }
    },
    //checkox
    check1(e){
      let thisName=e.currentTarget.name;
      if(this.myCheckList.indexOf(thisName)===-1){
        this.myCheckList.push(thisName)
      }else{
        this.myCheckList.splice(this.myCheckList.indexOf(thisName),1)
      }
      console.log(this.myCheckList)
    },


    getIntroduce(id){
      this.projectList=[];
        let arr = this.tableData3;
        for(let i=0; i<arr.length; i++){
           if(arr[i].id==id){
               this.projectList.push(arr[i].introduce);
           }
        }
    },
    submitForm(formName) {
      let check = true;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          return
        } else {
          check = false;
        }
      });
      return check;
    },
    handleClose(){
      this.$emit('changeClose',false);
    },
  },
  computed:{
    allCheck(){
      var allCheck=[];
      return allCheck.concat(this.myContactsCheck,this.netContactsCheck)
    },
    allShow(){
      var allShow=[];
      return allShow.concat(this.myContactsShow,this.netContactsShow)
    },
  },
  mounted() {
    this.list = this.states.map(item => {
      return { value: item, label: item };
    });
  },
  created(){

  },
  watch:{
    dialogPush:function(e){
      this.project_name=this.proName;
      this.project_id=this.proid;
      this.initData();
    },
  }
}
</script>

<style lang="less">
  @import '../../../assets/css/mycontacts';
</style>



